.weight-list {
  display: grid;
  grid-template-columns: 570px 570px;
  gap: 80px;
  margin-left: 110px;
  margin-right: 110px;
  list-style: none;
  padding: 0;
}

.weight-list__item {
  width: 518px;
  height: 374px;
  background-color: #F2F2F2;
  padding-left: 52px;
}

.weight-list__item-caption {
  font-family: Oswald, sans-serif;
  font-size: 36px;
  font-weight: 400;
  line-height: 36px;
  text-align: left;
}

.weight-list__item-link {
  font-family: Oswald, sans-serif;
  font-size: 20px;
  font-weight: 400;
  line-height: 30px;
  text-align: center;
  color: #000000;
  text-transform: uppercase;
}

.weight-list__item-text {
  font-family: Arial;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  text-align: left;

}
